.login-wrap-main {
  width: 100%;
  height: 100%;
  display: flex;
  box-sizing: border-box;
  background: #f6f7fb;
  position: relative;
  padding-top: 84px;
}
.login-wrap-main .width-main {
  width: 1200px;
  margin: 0 auto;
}
.login-wrap-main .login-top {
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background: #FFFFFF;
  line-height: 84px;
  z-index: 2;
  align-items: center;
  height: 84px;
}
.login-wrap-main .login-top .logo {
  width: 300px;
  display: inline-block;
  height: auto;
  float: left;
  margin-top: 25px;
}
.login-wrap-main .login-top .logo img {
  width: 100%;
  height: 100%;
}
.login-wrap-main .login-top .text-right {
  float: right;
  display: flex;
  text-align: right;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  align-items: center;
  color: #000000;
  cursor: pointer;
}
.login-wrap-main .login-top .text-right .icon-img {
  font-size: 25px;
  margin-right: 10px;
  color: #0D1731;
  display: inline-block;
}
.login-wrap-main .login-top .text-right img {
  width: 25px;
  margin-right: 10px;
}
.login-wrap-main .login-top .text-right:hover {
  color: #034EA2;
}
.login-wrap-main .login-top .text-right:hover .icon-img {
  color: #034EA2;
}
.login-wrap-main .main-warptext {
  background: url(../../assets/images/login-bei.png) no-repeat top right #f6f7fb;
  width: 100%;
  display: block;
}
.login-wrap-main .content-wrap {
  display: flex;
  align-items: center;
  justify-content: right;
  width: 100%;
  position: relative;
  height: calc(100% - 63px);
}
.login-wrap-main .img-left {
  width: 52%;
  margin: 0 46px 0 0;
  float: left;
  position: absolute;
  left: 46px;
  margin-top: -1%;
}
.login-wrap-main .img-left video::-webkit-media-controls {
  display: none !important;
}
.login-wrap-main .img-left video {
  max-width: 100%;
  outline: none;
}
.login-wrap-main .main-login-wrap {
  width: 38%;
  float: right;
  background: #FFFFFF;
  box-shadow: 4px 6px 39px rgba(101, 138, 193, 0.2);
  border-radius: 10px;
  padding: 4% 5% 3%;
  margin-top: -4%;
}
.login-wrap-main .main-login-wrap .login-title {
  display: block;
  width: 100%;
  margin-bottom: 30px;
}
.login-wrap-main .main-login-wrap .login-title div {
  display: inline-block;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  align-items: center;
  color: #231F20;
  padding-bottom: 15px;
  position: relative;
}
.login-wrap-main .main-login-wrap .login-title div::after {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 4px;
  background: #034EA2;
  border-radius: 4px;
  content: "";
}
.login-wrap-main .main-login-wrap .form-it {
  margin-bottom: 30px!important;
}
.login-wrap-main .main-login-wrap .form-it p {
  font-weight: normal;
  font-size: 18px;
  line-height: 14px;
  align-items: center;
  color: #000000;
  display: block;
  margin-bottom: 0;
}
.login-wrap-main .main-login-wrap .form-it .ant-input-affix-wrapper {
  margin-top: 10px;
}
.login-wrap-main .main-login-wrap .form-it .ant-input-affix-wrapper .ant-input {
  border: 1px solid #D6DFE6;
  box-sizing: border-box;
  border-radius: 5px;
}
.login-wrap-main .main-login-wrap .form-it-last {
  margin-bottom: 40px!important;
}
.login-wrap-main .main-login-wrap .login-form-button {
  width: 100%;
  display: block;
  background: #034EA2;
  border-radius: 5px;
  color: #fff;
  border: none;
  line-height: 40px;
  height: 40px;
}
.login-wrap-main .footer-p {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  display: block;
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: 48px;
  align-items: center;
  color: #0D1731;
  opacity: 0.3;
}
.login-wrap-main .footer-p a {
  color: #0D1731;
}
.login-wrap {
  width: 100%;
  height: 100%;
  display: flex;
  box-sizing: border-box;
  background-size: 100% 100%;
}
.login-wrap .content-wrap {
  margin: auto;
  display: flex;
}
.login-wrap .img-left {
  width: 880px;
  background-size: 100% 100%;
  height: 675px;
  box-shadow: 0px 0 26px rgba(107, 110, 137, 0.3);
  border-radius: 21px;
  margin-right: 30px;
}
.img-user {
  width: 21px;
  height: 23px;
}
.img-password {
  width: 17px;
  height: 17px;
}
.teaching-login-wrap .login-title div {
  text-align: left;
  font-size: 30px;
  margin-left: 34px;
  margin-top: 90px;
  word-spacing: normal;
}
.teaching-login-wrap .login-title p {
  width: 261px;
  height: 50px;
  font-size: 20px;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #999999;
  line-height: 50px;
  margin-left: 34px;
}
.teaching-login-wrap {
  width: 423px;
  height: 675px;
  box-shadow: 0px 10px 10px rgba(107, 110, 137, 0.07);
  border-radius: 21px;
  background: #fff;
  /*更改谷歌浏览器input背景*/
}
.teaching-login-wrap .login-title {
  text-align: center;
  margin-top: 80px;
}
.teaching-login-wrap .login-title div {
  font-size: 30px;
  margin-top: 30px;
  color: #333333;
}
.teaching-login-wrap .login-title p {
  width: 261px;
  height: 21px;
  font-size: 20px;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #999999;
  line-height: 37px;
  margin-top: 20px;
  margin-bottom: 80px;
}
.teaching-login-wrap .login-form {
  padding: 0 34px;
  border: none;
  border-radius: 0;
}
.teaching-login-wrap .login-form .text-span {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  display: inline-block;
  color: #034EA2;
  margin-top: 14px;
}
.teaching-login-wrap .login-form .text-span a {
  font-size: 14px;
  line-height: 14px;
  display: block;
  color: #034EA2;
}
.teaching-login-wrap .login-form .forget-password {
  text-align: right;
  cursor: pointer;
  display: inline-block;
}
.teaching-login-wrap .ant-btn {
  width: 100%;
  height: 60px;
  background: linear-gradient(90deg, #6fb2ff, #3e78ed);
  box-shadow: 0px 7px 17px 0px rgba(50, 129, 255, 0.2);
  border-radius: 30px;
  margin-top: 90px;
  color: #fff;
  font-size: 22px;
}
.teaching-login-wrap .ant-input {
  background: #fff;
  border: none;
  border-bottom: 1px solid #000;
  border-radius: none!important;
  outline: none;
}
.teaching-login-wrap .ant-input-affix-wrapper .ant-input:not(:first-child) {
  border-bottom: 1px solid #ccc;
  background: #fff;
  margin-left: 10px;
  outline: none;
  border-radius: none!important;
}
.teaching-login-wrap input:-webkit-autofill,
.teaching-login-wrap input:-webkit-autofill:hover,
.teaching-login-wrap input:-webkit-autofill:focus,
.teaching-login-wrap input:-webkit-autofill:active {
  /*延迟背景颜色载入*/
  -webkit-transition-delay: 99999s;
  -webkit-transition: color 99999s ease-out, background-color 99999s ease-out;
}
.ant-input-affix-wrapper .ant-input:focus {
  box-shadow: none;
  border-radius: none;
}
.titles-login {
  background: url('../../assets/images/logintitles.png') no-repeat;
  height: 300px;
  background-position: 80px 80px;
}
.ant-input-prefix {
  border: none;
  outline: none;
}
.ant-form-item {
  border-radius: none !important;
}
.teaching-login-wrap .ant-input-affix-wrapper .ant-input:not(:first-child) {
  border-radius: 0;
}
.login-mainwarp .text-span {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  display: inline-block;
  color: #034EA2;
  margin-top: 14px;
}
.login-mainwarp .text-span a {
  font-size: 14px;
  line-height: 14px;
  display: block;
  color: #034EA2;
}
.login-mainwarp .forget-password {
  text-align: right;
  cursor: pointer;
  display: inline-block;
  float: right;
  margin-top: 14px;
  margin-bottom: 0;
}
.forget-password {
  text-align: right;
  cursor: pointer;
}
